import Experience from "../../../Experiance.js";
import * as THREE from "three";
import Scenario from "../../../Scenario.js";
import PlanetBase from "./Planet.js";

export default class Planet3 extends PlanetBase {

    constructor() {
        super();
        this.experiance = Experience.INSTANCE;
        this.scene = this.experiance.scene;
        this.resources = this.experiance.resources;
        this.setMesh()
        this.watchConfig();
    }

    setMesh() {
        this.mesh = this.resources.items.planet3.scene.children[0];
        this.mesh.position.set(-4, -6.7, 0);


        this.group = new THREE.Group()
        this.group.add(this.mesh);

        this.scene.add(this.group);
    }

    update() {
        this.updateScale();
        this.mesh.rotation.z += 0.0015
    }

    watchConfig() {
        this.setConfig()
        this.experiance.config.on(Scenario.CHAPTER_CHANGE, () => this.setConfig())
    }

    setConfig() {
        this.targetScale = this.experiance.config.stage.planets?.planet3.scale;
    }
}