import Experience from "../../Experiance.js";
import * as THREE from "three";
import DescriptionText from "../Texts/DescriptionText.js";
import Scenario from "../../Scenario.js";
import ActorHologramRound from "./ActorHologramRound.js";
import VideoPlane from "../planes/VideoPlane.js";

// export const NeonColor = 0x00B0D1
// export const NeonColor = 0x0025AA
const NeonColor = 0x0000FD

export default class Actor {
    targetScale = 0
    targetX = 0
    targetOpacity = 0
    targetActor = ''

    constructor() {
        this.experiance = Experience.INSTANCE;
        this.scene = this.experiance.scene;
        this.resources = this.experiance.resources;
        this.setMaterial()
        this.setMesh()
        this.watchConfig();
    }

    setMaterial() {

    }

    setMesh() {
        this.video = new VideoPlane();
        this.video.mesh.position.y = 2;
        this.video.mesh.rotation.x = -Math.PI / 16;

        this.zombieYelling = this.resources.items.zombieArgueModel;
        // this.zombieYelling = this.resources.items.zombieArgueModel;
        this.zombieYelling.position.set(11, -8.5, 5);
        this.zombieYelling.rotation.y = -Math.PI / 12;

        this.zombieAgony = this.resources.items.zombieAgonyModel;
        // this.zombieAgony = this.resources.items.zombieArgueModel;
        this.zombieAgony.position.set(11, -8.5, 5);
        this.zombieAgony.rotation.y = -Math.PI / 12;

        this.zombieArgue = this.resources.items.zombieArgueModel;
        this.zombieArgue.position.set(11, -8.5, 5);

        this.zombieVictory = this.resources.items.zombieVictoryModel;
        // this.zombieVictory = this.resources.items.zombieArgueModel;
        this.zombieVictory.position.set(11, -8.5, 5);
        this.zombieVictory.rotation.y = -Math.PI / 12;

        this.zombieSalute = this.resources.items.zombieSaluteModel;
        // this.zombieSalute = this.resources.items.zombieArgueModel;
        this.zombieSalute.position.set(11, -8.5, 5);
        this.zombieSalute.rotation.y = -Math.PI / 6;


        // this.hologram = new ActorHologram();
        this.hologram = new ActorHologramRound({color: NeonColor});

        this.laptop = this.resources.items.laptop.scene.children[0].children[0].children[0].children[0];
        this.laptop.scale.set(13, 13, 13);
        this.laptop.position.set(0, -12, 12);

        this.actorStand = this.resources.items.actorStand.scene.children[0];

        this.text = new DescriptionText({isTexture: false});
        this.text.group.rotation.x = -Math.PI / 15;
        this.text.group.position.z = -10;

        this.group = new THREE.Group()

        this.group.add(this.video.mesh)

        this.group.add(this.zombieYelling)
        this.group.add(this.zombieArgue)
        this.group.add(this.zombieAgony)
        this.group.add(this.zombieVictory)
        this.group.add(this.zombieSalute)

        this.group.add(this.actorStand)
        this.group.add(this.laptop)
        this.group.add(this.text.group)

        this.group.add(this.hologram.group)
        this.group.scale.set(this.targetScale, this.targetScale, this.targetScale)
        this.group.position.z = 12.5
        this.scene.add(this.group);
    }

    update() {
        this.updateScale();
        this.updatePosition();
        this.updateZombie();
        this.video?.update();
        this.hologram?.update();
    }

    watchConfig() {
        this.setConfig()
        this.experiance.config.on(Scenario.CHAPTER_CHANGE, () => this.setConfig())
    }

    setConfig() {
        this.targetScale = this.experiance.config.stage.description.scale;
        if (!this.targetScale) {
            this.targetX = -400
        } else {
            this.targetX = 0
        }
        this.targetActor = this.experiance.config.stage.description.activeActor;
        this.targetOpacity = this.experiance.config.stage.description.targetOpacity || 0;
    }

    updateScale() {
        const deltaX = this.targetScale - this.group.scale.x;
        const deltaY = this.targetScale - this.group.scale.y;
        const deltaZ = this.targetScale - this.group.scale.z;

        if (Math.abs(deltaX) > 0.0001 || Math.abs(deltaY) > 0.0001 || Math.abs(deltaZ) > 0.0001) {
            const stepScale = this.group.scale.x + deltaX / 15;
            this.group.scale.set(stepScale, stepScale, stepScale);
        }
    }

    updatePosition() {
        this.deltaX = this.targetX - this.group.position.x;

        if (this.targetX === 0 && Math.abs(this.deltaX) > 0.1) {
            this.group.position.x += this.deltaX / 5;
        } else if (Math.abs(this.deltaX) > 0.1) {
            this.group.position.x += this.deltaX / 50;
        }
    }

    updateZombie() {
        if (!this.zombieArgue || !this.zombieYelling || !this.zombieAgony || !this.zombieVictory) {
            return
        }

        if (this.targetActor === this.activeActor) {
            return
        }

        this.activeActor = this.targetActor

        this.zombieYelling.scale.set(0, 0, 0)
        this.zombieArgue.scale.set(0, 0, 0)
        this.zombieAgony.scale.set(0, 0, 0);
        this.zombieVictory.scale.set(0, 0, 0);
        this.zombieSalute.scale.set(0, 0, 0);

        if (this.activeActor === 'zombieYelling') {
            this.zombieYelling.scale.set(0.05, 0.05, 0.05);
            this.updateStand(this.zombieYelling.position);
        }
        if (this.activeActor === 'zombieAgony') {
            this.zombieAgony.scale.set(0.06, 0.06, 0.06);
            this.updateStand(this.zombieAgony.position);
        }
        if (this.activeActor === 'zombieArgue') {
            this.zombieArgue.scale.set(0.06, 0.06, 0.06);
            this.updateStand(this.zombieArgue.position);
        }
        if (this.activeActor === 'zombieVictory') {
            this.zombieVictory.scale.set(0.06, 0.06, 0.06);
            this.updateStand(this.zombieVictory.position);
        }
        if (this.activeActor === 'zombieSalute') {
            this.zombieSalute.scale.set(0.06, 0.06, 0.06);
            this.updateStand({
                ...this.zombieSalute.position,
                z: this.zombieSalute.position.z - 1
            });
        }

        if (!this.activeActor) {
            this.hologram.group.scale.set(0, 0, 0)
            this.actorStand.scale.set(0, 0, 0)
        } else {
            this.hologram.group.scale.set(1, 1, 1)
            this.actorStand.scale.set(1, 1, 1)
        }
    }

    updateStand(actorPosition) {
        this.hologram.group.position.set(
            actorPosition.x,
            actorPosition.y + 5.7,
            actorPosition.z + 1
        );
        this.actorStand.position.set(
            actorPosition.x,
            actorPosition.y - 0.5,
            actorPosition.z + 1
        );
    }
}