import Experience from "../../Experiance.js";
import * as THREE from 'three';
import {TextGeometry} from "three/examples/jsm/geometries/TextGeometry.js";
import Scenario from "../../Scenario.js";

export default class DescriptionText {
    geometries = []
    meshes = []

    constructor(config, startX = -2, startY = 6, z = 10, configName = 'description', firstBig = true) {
        this.configName = configName;
        this.config = {
            isTexture: true,
            textureName: 'textureBrownRing',
            ...config
        };
        this.firstBig = firstBig;
        this.experiance = Experience.INSTANCE;
        this.scene = this.experiance.scene;
        this.resources = this.experiance.resources;
        this.startX = startX;
        this.startY = startY;
        this.z = z;
        this.group = new THREE.Group();
        this.setConfig()

        this.experiance.config.on(Scenario.CHAPTER_CHANGE, () => setTimeout(() => this.setConfig(), 100))
    }

    setConfig() {
        this.content = [...this.experiance.config.stage[this.configName].content];
        this.color = this.experiance.config.stage[this.configName].color;
        this.setGeometry();
        this.setTextures();
        this.setMaterial();
        this.setMesh();
    }

    setGeometry() {
        this.geometries = this.content.map((line, ndx, array) => {
            const rowBigger = ndx === 0 && this.firstBig;
            const y = rowBigger
                ? this.startY + 2
                : this.startY - ndx * 0.6;

            return {
                geometry: this.generateGeometry(line, rowBigger),
                position: {x: this.startX, y, z: this.z}
            }
        })
    }

    setTextures() {
        this.textures = {}
        this.textures.color = this.resources.items[this.config.textureName];
        this.textures.color.colorSpace = THREE.SRGBColorSpace
    }

    setMaterial() {
        this.material = new THREE.MeshStandardMaterial({
            map: this.config.isTexture ? this.textures.color : undefined,
            color: !this.config.isTexture ? this.color : undefined,
        });

        this.material.needsUpdate = true
    }

    setMesh() {
        if (this.geometries?.length) {
            this.resetMeshes()
        }
    }

    resetMeshes() {
        this.meshes.forEach(mesh => this.group.remove(mesh))

        this.meshes = [
            ...this.geometries.map(item => {
                const mesh = new THREE.Mesh(item.geometry, this.material);
                mesh.clp_bloomed = true;
                mesh.position.set(item.position.x, item.position.y, item.position.z)
                return mesh;
            })
        ]

        this.meshes.forEach(mesh => this.group.add(mesh));
    }


    generateGeometry(content, isBigger) {
        const geometry = new TextGeometry(content, {
            font: this.resources.items.kenpixel_ttf,
            size: isBigger ? 0.6 : 0.35,
            height: 0.1,
            curveSegments: 12,
            bevelEnabled: true,
            bevelThickness: 0.01,
            bevelSize: 0.02,
            bevelOffset: 0,
            bevelSegments: 5
        })
        geometry.computeBoundingBox()
        geometry.translate(
            -geometry.boundingBox.max.x * 0.5,
            -geometry.boundingBox.max.y * 0.5,
            -geometry.boundingBox.max.z * 0.5
        )
        return geometry;
    }

    update() {
    }
}