import Experience from "../Experiance.js";
import * as THREE from "three";
import EventEmitter from "./EventEmmiter.js";

export default class Raycaster extends EventEmitter {
    static ON_HOVER_EVENT = 'hover'
    constructor() {
        super();
        this.experience = Experience.INSTANCE;
        this.camera = this.experience.camera;
        this.mouse = this.experience.mouse;
        this.instance = new THREE.Raycaster()
        this.intersects = [];
    }

    update() {
        this.instance.setFromCamera(this.mouse.instance, this.camera.instance)
        const objects = []
        if(this.experience.world.mainNucleus?.nucleus) {
             objects.push(this.experience.world.mainNucleus?.nucleus)
        }
        if(this.experience.world.redNucleus?.nucleus) {
             objects.push(this.experience.world.redNucleus?.nucleus)
        }
        if(this.experience.world.yellowNucleus?.nucleus) {
             objects.push(this.experience.world.yellowNucleus?.nucleus)
        }
        if(this.experience.world.greenNucleus?.nucleus) {
             objects.push(this.experience.world.greenNucleus?.nucleus)
        }
        if(this.experience.world.orangeNucleus?.nucleus) {
             objects.push(this.experience.world.orangeNucleus?.nucleus)
        }
        if(this.experience.world.pinkNucleus?.nucleus) {
             objects.push(this.experience.world.pinkNucleus?.nucleus)
        }
        if(this.experience.world.purpleNucleus?.nucleus) {
             objects.push(this.experience.world.purpleNucleus?.nucleus)
        }
        this.intersects = this.instance.intersectObjects(objects);
    }
}