import * as THREE from "three";
import DefaultNucleus from "./DefaultNucleus.js";
import Scenario from "../../Scenario.js";

export default class MainNucleus extends DefaultNucleus {
    blobScale = 0.4

    constructor() {
        super();
        this.mouse = this.experiance.mouse;
        this.setGeometry();
        this.setTextures('textureNucleus');
        this.setMaterial();
        this.setMesh();

        this.watchConfig();
    }

    setTextures(textureName) {
        super.setTextures(textureName)
    }

    setMesh() {
        super.setMesh()
        this.scene.add(this.nucleus);
    }

    watchConfig() {
        this.targetScale = this.experiance.config.stage.nucleus.main.scale;

        this.experiance.config.on(Scenario.CHAPTER_CHANGE, () => {
            this.targetScale = this.experiance.config.stage.nucleus.main.scale;
        })
    }

    update() {
        if(!this.nucleus) {
            return
        }

        super.update();
    }

    rotate() {
        if (this.isCameraInside()) {
        } else {
            this.nucleus.rotation.y += 0.006;
        }
    }

    isCameraInside() {
        return this.experiance.camera.instance.position.z < 0;
    }
}

