import Experience from "../../Experiance.js";
import * as THREE from "three";
import Scenario from "../../Scenario.js";

export default class VideoPlane {
    constructor() {
        this.experiance = Experience.INSTANCE;
        this.scene = this.experiance.scene;
        this.resources = this.experiance.resources;
        this.setGeometry();
        this.setTextures();
        this.setMaterial();
        this.setMesh();

        this.watchConfig();
    }

    setGeometry() {
        this.geometry = new THREE.PlaneGeometry(30,20,10)
    }

    setTextures() {
        this.textures = {}
        this.video = document.getElementById('zombie-video');
        this.textures.color = new THREE.VideoTexture(this.video);
        this.textures.color.colorSpace = THREE.SRGBColorSpace
        this.textures.color.minFilter = THREE.LinearFilter;
        this.textures.color.magFilter = THREE.LinearFilter;
        this.textures.color.needsUpdate = true
    }

    setMaterial() {
        this.material = new THREE.MeshStandardMaterial({
            map: this.textures.color,
            side: THREE.DoubleSide
        })
    }

    setMesh() {
        this.mesh = new THREE.Mesh(this.geometry, this.material)
    }

    watchConfig() {
        this.setConfig()
        this.experiance.config.on(Scenario.CHAPTER_CHANGE, () => this.setConfig())
    }

    setConfig() {
        this.videoOn = this.experiance.config.stage.description.video.on;
        if (this.videoOn) {
            this.mesh.scale.set(1,1,1);
        } else {
            this.mesh.scale.set(0,0,0);
        }
        this.update()
    }

    update() {
        if(!this.videoOn) {
            this.video?.pause();
        }

        if (!this.isVideoPlaying && this.videoOn) {
            this.video?.play();
        }
    }

    get isVideoPlaying() {
        return !!(this.video.currentTime > 0 && !this.video.paused && !this.video.ended && this.video.readyState > 2);
    }


}