import * as THREE from "three";
import Experience from "../../Experiance.js";

export default class DefaultPlanet {
    constructor(textureName, position, speed) {
        this.experiance = Experience.INSTANCE;
        this.scene = this.experiance.scene;
        this.resources = this.experiance.resources;
        this.time = this.experiance.time;
        this.position = position;
        this.speed = speed;
        this.setGeometry();
        this.setTextures(textureName);
        this.setMaterial();
        this.setMesh();
    }

    setGeometry() {
        this.geometry = new THREE.SphereGeometry(3, 32, 32)
    }

    setTextures(textureName) {
        this.textures = {}
        this.textures.color = this.resources.items[textureName];
        this.textures.color.colorSpace = THREE.SRGBColorSpace
    }

    setMaterial() {
        this.lambertMaterial = new THREE.MeshStandardMaterial({
            map: this.textures.color
        });
    }

    setMesh() {
        this.mesh = new THREE.Mesh(this.geometry, this.lambertMaterial);
        this.mesh.position.set(this.position.x, this.position.y, this.position.z)
        this.mesh.rotation.x = Math.PI * Math.random();
        this.scene.add(this.mesh)
    }

    update() {
        if(!this.position) {
            return
        }

        this.mesh.rotation.y += 0.006;

        const orbitRadius = Math.sqrt(this.position.x * this.position.x + this.position.y * this.position.y + this.position.z * this.position.z)

        this.mesh.position.x = (-Math.cos(this.time.elapsed * 0.00001 * this.speed) * orbitRadius) + 1;
        this.mesh.position.z = -Math.sin(this.time.elapsed * 0.00001 * this.speed) * orbitRadius;

    }
}