import * as THREE from 'three';
import Experience from "./Experiance.js";
import {OrbitControls} from "three/examples/jsm/controls/OrbitControls.js";
import Scenario from "./Scenario.js";

export default class Camera {
    targetPosition = {
        x: 0,
        y: 0,
        z: 15
    }

    constructor() {
        this.experience = Experience.INSTANCE;
        this.sizes = this.experience.sizes;
        this.scene = this.experience.scene;
        this.canvas = this.experience.canvas;
        this.setInstance();
        this.setOrbitControls();
        this.watchConfig();
    }

    setInstance() {
        this.instance = new THREE.PerspectiveCamera(35, this.sizes.width / this.sizes.height, 0.1, 1000)
        this.scene.add(this.instance)
    }

    setOrbitControls() {
        this.controls = new OrbitControls(this.instance, this.canvas);
        this.controls.enableDamping = true;
        this.controls.maxDistance = 70;
        this.controls.minDistance = 3.4;
        this.controls.enablePan = false;
    }

    resize() {
        this.instance.aspect = this.sizes.width / this.sizes.height;
        this.instance.updateProjectionMatrix();
    }

    watchConfig() {
        this.setConfig()
        this.experience.config.on(Scenario.CHAPTER_CHANGE, () => this.setConfig())
    }

    setConfig() {
        this.targetPosition = this.experience.config.stage.camera.position;
        this.locked = this.experience.config.stage.camera.locked;
    }


    update() {
        this.updatePosition();
        this.controls.update();
    }

    updatePosition() {
        const deltaX = (this.targetPosition.x - this.instance.position.x) / 30;
        const deltaY = (this.targetPosition.y - this.instance.position.y) / 30;
        const deltaZ = (this.targetPosition.z - this.instance.position.z) / 30;

        if (Math.abs(deltaX) > 0.1 || Math.abs(deltaY) > 0.1 || Math.abs(deltaZ) > 0.1) {
            this.instance.position.set(
                this.instance.position.x + deltaX,
                this.instance.position.y + deltaY,
                this.instance.position.z + deltaZ
            )
            this.instance.updateProjectionMatrix();
        }
    }
}